.Bottom{
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: auto;
    flex: 1 1 0;
    ::-webkit-scrollbar {
        display: none;
    }
    .header{
        // background-color: rgb(222, 153, 25);
        //solarized dark
        color: whitesmoke;
        background-color: rgb(0, 43, 54);
        height: 2rem;
        display: flex;
        align-items: center;
        padding-left: 15px;
        .outputTools{
            margin-left: auto;
            margin-right: 10px;
            display: flex;
            gap: 6px;
            & > *{
                transition: transform 0.3s ease-in-out;
                cursor: pointer;
                &:hover{
                    filter: brightness(2);
                    transform: scale(1.2);
                }
                &:active{
                    // box-shadow: 0 0 0 0.2rem rgba(0, 0, 0, 0.25);
                    transform: scale(0.4);
                }
            }
        }
    }
    .bottomContainer{
        overflow: auto;
        height: 100%;
        padding: 5px 15px;
        text-align: justify;
        white-space: pre;
        position: relative;
        border: 3px solid rgb(0, 43, 54);
        // &::before{
        //     content: "";
        //     display: block;
        //     height: 100%;
        //     position: absolute;
        //     left: 4px;
        //     background-color: red;
        //     width: 3px;
        //     z-index: 100;
        //     overflow: hidden;
        //   }
    }
}